export default {
  name: 'home',
  components: {
    // HelloWorld
  },
  data() {
    return {
      loading: false
    }
  },
  computed: {
    bgImage() {
      if(this.allRooms.length > 0) {
        return {
          backgroundImage: `url(${this.allRooms[this.currentRoomIndex].imageUrl})`
        }
      }
      return {}
    },
    currentRoomNumber() {
      // return this.currentRoomIndex + 1 < 10 ? '0' + (this.currentRoomIndex + 1) : this.currentRoomIndex + 1
      return this.allRooms[this.currentRoomIndex].index
    },
    currentRoomName() {
      return this.allRooms.length > 0 ? this.allRooms[this.currentRoomIndex].name : ''
    },
    allRooms() {
      return this.$store.getters.rooms
    },
    currentRoomIndex() {
      return this.$store.getters.currentRoomIndex
    }
  },
  created() {
    // this.getAllRooms()
  },
