    //     .then(() => {
    //       this.loading = false
    //     })
    // },
    handleHover(index) { // index: array index
      // console.log(index)
      this.$store.dispatch('setCurrentRoomIndex', index)
      // this.currentRoomIndex = index

    },
    handleClick(index) { // index: room property index
      this.$router.push({ name: 'room', params: { id: index } })
    }
  }
}
