import request from './request'

export function getRooms() {
  return request({
    url: '/rooms',
    method: 'get'
  })
}

export function getRoomDetail(id) {
  return request({
    url: `/room/${id}`,
    method: 'get'
  })
}

export function bookingRoom(id, params) {
  /**
   * params:
   * name (string): 訂房者名稱
   * tel (string): 訂房者電話
   * date[] (string): 訂房日期，格式 Y-m-d
   *   [
   *     "2019-08-01",
   *     "2019-08-02",
   *     "2019-08-03",
   *   ]
   */
  return request({
    url: `/room/${id}`,
    method: 'post',
    data: params
  })
}

export function deleteAllBooking() {
  return request({
    url: `/rooms`,
    method: 'delete'
  })
}
