import Vue from 'vue'
import Vuex from 'vuex'
import { getRooms } from '@/utils/api'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentRoomIndex: 0,
    rooms: [
      // { index: '', id: '', name: '' }
    ]
  },
  mutations: {
    GET_ALL_ROOMS: (state, items) => {
      items.forEach((item, index) => {
        item.index = index + 1 < 10 ? '0' + (index + 1) : index + 1
      });
      state.rooms = items
    },
    SET_CURRENT_INDEX: (state, index) => {
      state.currentRoomIndex = index
    }
  },
  actions: {
    getAllRooms(context) {
      return new Promise((resolve, reject) => {
        getRooms().then(response => {
          const items = response.items
          context.commit('GET_ALL_ROOMS', items)
          resolve()
        }).catch(error => {
          reject(error)
        })
      })
    },
    setCurrentRoomIndex(context, index) {
      context.commit('SET_CURRENT_INDEX', index)
    }
  },
  getters: {
    rooms: state => state.rooms,
    currentRoomIndex: state => state.currentRoomIndex
  }
})
